@import '../../../style/mixin.less';
.wrapper{
    height: 100%;
    position: relative;
    padding-bottom: 47px;
    &_container{
        height: 100%;
        overflow-y: auto;
        &_info{
            display: flex;
            align-items: center;
            &>li:first-child{
                flex: 1;
                color: @black;
                &>div{
                    width: 30px;
                    height: 30px;
                    margin-right: 10px;
                    border-radius: 50%;
                    background-color: @bgGrey;
                    img{
                        width: 100%;
                        height: 100%;
                        border-radius: 50%;
                    }
                }
            }
            &>li:last-child{
                &>i:first-child{
                    color: @green;
                    margin-right: 2px;
                }
                &>i:last-child{
                    color: @red;
                    margin-left: 10px;
                }
            }
            &>li{
                display: flex;
                align-items: center;
            }
        }
    }
    &_footer{
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
    }
}