.wrapper {
  height: 100%;
  position: relative;
  padding-bottom: 47px;
}
.wrapper_container {
  height: 100%;
  overflow-y: auto;
}
.wrapper_container_info {
  display: flex;
  align-items: center;
}
.wrapper_container_info > li:first-child {
  flex: 1;
  color: #000;
}
.wrapper_container_info > li:first-child > div {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  border-radius: 50%;
  background-color: #F7F7F7;
}
.wrapper_container_info > li:first-child > div img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.wrapper_container_info > li:last-child > i:first-child {
  color: #2FCE31;
  margin-right: 2px;
}
.wrapper_container_info > li:last-child > i:last-child {
  color: #FF0000;
  margin-left: 10px;
}
.wrapper_container_info > li {
  display: flex;
  align-items: center;
}
.wrapper_footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
}
